import React from "react";
import MainLogo from "../images/logo.png";
import "./home.css";
import Particles from "react-particles-js";
import SectionWipes2 from "./SectionWipes2.js";

class Home extends React.Component {
    // constructor() {
    //     super();
    //     this.handleHover = this.handleHover.bind(this);
    // }

    componentDidMount() {
        document.querySelector(".nav-home").classList.add("current");

        const disappear = () => {
            var presentScroll = window.pageYOffset;
            var offset = 200;
            var opacity;

            if (presentScroll <= offset) {
                opacity = 1 - presentScroll / offset;
            } else {
                opacity = 0;
            }

            document.querySelector(".Home-Logo").style.opacity = opacity;
        };

        window.onscroll = function () {
            console.log(this.location.href);
            if (this.location.path == "/") {
                disappear();
            }
        };

        //Hover Effect
        const image = document.querySelector(".Home-Logo");
        const container = document.querySelector(".Particles");
        const handleHover = (e) => {
            let xAxis = (window.innerWidth / 2 - e.pageX) / 30;
            let yAxis = (window.innerHeight / 2 - e.pageY) / 30;
            image.style.transform = `rotateX(${yAxis}deg) rotateY(${-xAxis}deg)`;
        };
        //Parallax
        container.addEventListener("mousemove", (e) => {
            {
                handleHover(e);
            }
        });
        container.addEventListener("mouseleave", (e) => {
            image.style.transform = `rotateY(0deg) rotateX(0deg)`;
        });
    }

    componentWillUnmount() {
        document.querySelector(".nav-home").classList.remove("current");
    }

    render() {
        return (
            <div className="Home">
                <div class="container Logo-Perspective">
                    <img class="Home-Logo" src={MainLogo} alt="CSI LOGO" />
                </div>

                <div className="Section-Wipes-Container">
                    <SectionWipes2 />
                </div>

                <div class="Particles">
                    <Particles
                        params={{
                            particles: {
                                number: {
                                    value: 160,
                                    density: {
                                        enable: false,
                                    },
                                },
                                size: {
                                    value: 3,
                                    random: true,
                                    anim: {
                                        enable: false,
                                        speed: 4,
                                        size_min: 0.3,
                                        sync: false,
                                    },
                                },
                                line_linked: {
                                    enable: false,
                                },
                                move: {
                                    random: true,
                                    speed: 1,
                                    direction: "top",
                                    out_mode: "out",
                                },
                            },
                            interactivity: {
                                events: {
                                    onhover: {
                                        enable: true,
                                        mode: "bubble",
                                    },
                                    onclick: {
                                        enable: true,
                                        mode: "repulse",
                                    },
                                },
                                modes: {
                                    bubble: {
                                        distance: 250,
                                        duration: 2,
                                        size: 0,
                                        opacity: 0,
                                    },
                                    repulse: {
                                        distance: 400,
                                        duration: 4,
                                    },
                                },
                            },
                        }}
                    />
                    v3.3.0
                </div>
            </div>
        );
    }
}

export default Home;
