import React from "react";
import fire from "../config/Fire";
import "./hero.css";

class Hero extends React.Component {
    state = {
        name: null,
        designation: null,
        profileImg: null,
        addFlag: false,
        removeFlag: false,
        modifyFlag: false,
        key: null,
    };

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value,
        });
    };

    handleAddSubmit = (e) => {
        var tempId = Math.floor(Math.random() * 100);

        e.preventDefault();

        const memRef = fire.database().ref("members");
        const temp = memRef.push({
            id: tempId,
            name: this.state.name,
            profileImg: this.state.profileImg,
            designation: this.state.designation,
        });
    };

    handleModifySubmit = (e) => {
        e.preventDefault();

        const memRef = fire.database().ref("members" + "/" + this.state.key);
        memRef.update({
            name: this.state.name,
            profileImg: this.state.profileImg,
            designation: this.state.designation,
        });

        e.target.value = "";
    };

    handleRemoveSubmit = (e) => {
        e.preventDefault();

        const memRef = fire.database().ref("members" + "/" + this.state.key);
        memRef.remove();

        e.target.value = "";
    };

    addClick() {
        this.setState(
            {
                addFlag: true,
                removeFlag: false,
                modifyFlag: false,
            },
            () => {
                console.log(this.state);
            }
        );
    }

    removeClick() {
        this.setState(
            {
                addFlag: false,
                removeFlag: true,
                modifyFlag: false,
            },
            () => {
                console.log(this.state);
            }
        );
    }

    modifyClick() {
        this.setState(
            {
                addFlag: false,
                removeFlag: false,
                modifyFlag: true,
            },
            () => {
                console.log(this.state);
            }
        );
    }

    render() {
        return (
            <section className="hero">
                <nav>
                    <button class="invisible-button">Logout</button>
                </nav>
                <div className="Buttons">
                    <button
                        onClick={() => {
                            this.addClick();
                        }}
                    >
                        {" "}
                        Add Member
                    </button>
                    <button
                        onClick={() => {
                            this.removeClick();
                        }}
                    >
                        {" "}
                        Remove Member
                    </button>
                    <button
                        onClick={() => {
                            this.modifyClick();
                        }}
                    >
                        {" "}
                        Modify Member
                    </button>
                </div>

                <div className="Buttons ButtonsChildren">
                    {this.state.addFlag ? (
                        <div className="AddButtons">
                            <form onSubmit={this.handleAddSubmit}>
                                <label htmlFor="name">Name: </label>
                                <input
                                    type="text"
                                    id="name"
                                    onChange={this.handleChange}
                                ></input>

                                <label htmlFor="designation">
                                    Designation:{" "}
                                </label>
                                <input
                                    type="text"
                                    id="designation"
                                    onChange={this.handleChange}
                                ></input>

                                <label htmlFor="profileImg">
                                    Profile Picture:{" "}
                                </label>
                                <input
                                    type="text"
                                    id="profileImg"
                                    onChange={this.handleChange}
                                ></input>

                                <button> Submit </button>
                            </form>
                        </div>
                    ) : (
                        <></>
                    )}

                    {this.state.modifyFlag ? (
                        <div className="ModifyButtons">
                            <form onSubmit={this.handleModifySubmit}>
                                <label htmlFor="key">
                                    Enter Member Private key
                                </label>
                                <input
                                    type="text"
                                    id="key"
                                    onChange={this.handleChange}
                                ></input>

                                <label htmlFor="name">New Name: </label>
                                <input
                                    type="text"
                                    id="name"
                                    onChange={this.handleChange}
                                ></input>

                                <label htmlFor="designation">
                                    New Designation:{" "}
                                </label>
                                <input
                                    type="text"
                                    id="designation"
                                    onChange={this.handleChange}
                                ></input>

                                <label htmlFor="profileImg">
                                    New Profile Picture:{" "}
                                </label>
                                <input
                                    type="text"
                                    id="profileImg"
                                    onChange={this.handleChange}
                                ></input>

                                <button> Submit </button>
                            </form>
                        </div>
                    ) : (
                        <></>
                    )}

                    {this.state.removeFlag ? (
                        <div className="RemoveButtons">
                            <form onSubmit={this.handleRemoveSubmit}>
                                <label htmlFor="key">
                                    Enter Member Private key
                                </label>

                                <input
                                    type="text"
                                    id="key"
                                    onChange={this.handleChange}
                                ></input>

                                <button> Submit </button>
                            </form>
                        </div>
                    ) : (
                        <></>
                    )}
                </div>
                {/* */}
            </section>
        );
    }
}

export default Hero;
