import React from "react";
import { Link, NavLink, withRouter } from "react-router-dom";
import "./Navbar.css";
import Logo from "../images/logo.png";
import "bootstrap/dist/css/bootstrap.min.css";
import "jquery/dist/jquery.min.js";
import "bootstrap/dist/js/bootstrap.min.js";

class Navbar extends React.Component {
    componentDidMount() {
        // //Sticky Navbar
        // //When the user scrolls the page, execute myFunction
        // window.onscroll = function () {
        //     myFunction();
        // };
        // // Get the navbar
        // var navbar = document.querySelector(".navbar-collapse");
        // // Get the offset position of the navbar
        // var sticky = navbar.offsetTop;
        // // Add the sticky class to the navbar when you reach its scroll position. Remove "sticky" when you leave the scroll position
        // function myFunction() {
        //     if (window.pageYOffset >= sticky) {
        //         navbar.classList.add("sticky");
        //     } else {
        //         navbar.classList.remove("sticky");
        //     }
        // }
    }

    render() {
        return (
            <nav class="navbar navbar-expand-md navbar-light navbar-bg">
                <Link to="/">
                    <img className="navbar-brand Logo" src={Logo}></img>
                </Link>

                <button
                    class="navbar-toggler"
                    data-toggle="collapse"
                    data-target="#Menu"
                >
                    <span class="navbar-toggler-icon"></span>
                </button>

                <div id="Menu" className="collapse navbar-collapse">
                    <ul class="navbar-nav MenuItems mx-auto">
                        <li class="nav-home">
                            <Link to="/">
                                <span data-hover="Home">Home</span>
                            </Link>
                        </li>

                        <li class="nav-committee">
                            <Link to="/committee">
                                <span data-hover="Committee">Committee</span>
                            </Link>
                        </li>

                        <li class="nav-gallery">
                            <Link to="/gallery">
                                <span data-hover="Gallery">Gallery</span>
                            </Link>
                        </li>

                        <li class="nav-contact">
                            <Link to="/contact">
                                <span data-hover="Contact">Contact</span>
                            </Link>
                        </li>
                    </ul>

                    <ul className="navbar-nav MenuItems">
                        <li>
                            <Link to="/login">
                                {this.props.user ? (
                                    <span
                                        data-hover="Logout"
                                        onClick={this.props.handleLogout}
                                    >
                                        Logout
                                    </span>
                                ) : (
                                    <span data-hover="Login">Login</span>
                                )}
                            </Link>
                        </li>
                    </ul>
                </div>
            </nav>
        );
    }
}

export default withRouter(Navbar);
