import React from "react";
import fire from "../config/Fire";
import "./Contact.css";

class Contact extends React.Component {
    state = {
        name: null,
        email: null,
        message: null,
        key: null,
    };

    componentDidMount() {
        document.querySelector(".nav-contact").classList.add("current");
    }

    componentWillUnmount() {
        document.querySelector(".nav-contact").classList.remove("current");
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value,
        });
    };

    handleFormSubmit = (e) => {
        // var tempId = Math.floor(Math.random() * 100000);

        e.preventDefault();

        const conRef = fire.database().ref("contact");
        const temp = conRef.push({
            name: this.state.name,
            email: this.state.email,
            message: this.state.message,
        });
    };

    render() {
        return (
            <div>
                <h1 className="Contact-H1"> Contact</h1>
                <div className="AddContact">
                    <form onSubmit={this.handleFormSubmit}>
                        <label htmlFor="name">Name </label>
                        <input
                            type="text"
                            id="name"
                            onChange={this.handleChange}
                        ></input>

                        <label htmlFor="email">E-mail </label>
                        <input
                            type="text"
                            id="email"
                            onChange={this.handleChange}
                        ></input>

                        <label id="Message" htmlFor="message">
                            Message{" "}
                        </label>
                        <textarea
                            style={{ resize: "true" }}
                            type="text"
                            id="message"
                            onChange={this.handleChange}
                        ></textarea>

                        <button id="Sumit"> Submit </button>
                    </form>
                </div>
            </div>
        );
    }
}

export default Contact;
