import React from "react";
import fire from "../config/Fire";
import "./committee.css";

class Committee extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            members: [],
            events: [],
        };
    }

    componentDidMount() {
        const memRef = fire.database().ref("members");

        memRef.on("value", (snapshot) => {
            let mems = snapshot.val();
            let tempState = [];

            for (let mem in mems) {
                tempState.push({
                    id: mems[mem].id,
                    name: mems[mem].name,
                    profileImg: mems[mem].profileImg,
                    designation: mems[mem].designation,
                });
            }

            this.setState({ members: tempState }, () => {
                console.log(this.state.members);
            });
        });

        document.querySelector(".nav-committee").classList.add("current");
    }

    componentWillUnmount() {
        document.querySelector(".nav-committee").classList.remove("current");
    }

    render() {
        const memberData = this.state.members.map((member) => {
            return (
                <div class="card">
                    <br></br>

                    <img className="prof-avatar" src={member.profileImg}></img>
                    <br></br>
                    <br></br>
                    <h5 class="faculty-name"> {member.name} </h5>
                    <h6 class="faculty-designation">{member.designation}</h6>
                </div>
            );
        });

        return (
            <>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="card-deck">{memberData}</div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default Committee;
